h1,
h2,
h3,
h4,
p,
ul {
  margin: 0;
  padding: 0;
}

img {
  display: block;
  /* max-width: 100%; */
  height: auto;
}

.backdrop {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: scroll;
  background-color: rgba(17, 17, 17, 0.4);
  transition: opacity var(--transition-property),
    visibility var(--transition-property);
}

.is-hidden {
  display: none;
}

.modal-window {
  width: 100%;
  /* max-height: 90vh; */
  background-color: #fff;
  border: 2px solid #111;
  border-radius: 20px;
  position: fixed;
  top: 50%;
  left: 50%;
  overflow: auto;
  transform: translate(-50%, -50%);
  padding: 40px 24px;
}

/* @media screen and (min-width: 768px) and (max-width:1157px) */
/* Desktop styles */

/* @media screen and (min-width: 1158px)  */

@media screen and (min-width: 335px) {
  .modal-window {
    width: 335px;
  }
}

@media screen and (min-width: 768px) {
  .modal-window {
    width: 579px;
  }
}

.modal-close-btn {
  position: absolute;
  top: 7px;
  right: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 24px;
  padding: 0;
  background-color: var(--border-color);
  border: none;
  cursor: pointer;
  transition: background-color var(--transition-property),
    border var(--transition-property);
}

.modal-close-icon {
  stroke: #111111;
}

.modal-content {
  display: flex;
  flex-direction: column;
}
@media screen and (min-width: 768px) {
  .modal-close-btn {
    top: 24px;
    right: 24px;
  }
  .modal-content {
    flex-direction: row;
    gap: 24px;
  }
}

.modal-book-img {
  display: block;
}

@media screen and (min-width: 768px) {
  .modal-book-img {
    width: 192px;
    height: 281px;
  }
}

.modal-book-descr {
  margin-top: 14px;
}
.modal-book-title {
  font-size: 16px;
  font-weight: 700;
  line-height: 1.125;
  letter-spacing: -0.64px;
  text-transform: uppercase;
}

.modal-book-author {
  margin-top: 4px;
  font-size: 12px;
  font-style: italic;
  font-weight: 400;
  line-height: 1.17;
  letter-spacing: -0.48px;
}

.modal-book-review {
  margin-top: 18px;
  width: 287px;
  height: 70px;
  overflow-y: hidden;
}

.modal-book-links {
  margin-top: 14px;
}

.modal-btn {
  background-color: #fff;
  display: block;
  margin: 0 auto;
  padding: 14px 28px;
  border-radius: 40px;
  border: 2px solid #4f2ee8;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.29;
  letter-spacing: -0.14px;
  text-transform: uppercase;
}

@media screen and (min-width: 768px) {
  .modal-btn {
    margin-top: 40px;
    padding: 20px 0px;
    width: 499px;
  }
}
